.color-info {
  grid-area: 3 / 2;
  justify-self: end;
  -ms-flex-item-align: end;
      align-self: end;
  cursor: default;
}

.color-info__sample {
  margin: 1em;
  text-transform: uppercase;
  display: block;
  cursor: pointer;
}

.lock::before {
  position: absolute;
  content: '\1f512';
  -webkit-filter: saturate(0) invert(100%);
          filter: saturate(0) invert(100%);
}

@media (min-width: 600px) {
  .color-info {
    grid-area: 1 / 1;
    -ms-flex-item-align: start;
        align-self: start;
    width: 14rem;
  }
  .color-info__sample {
    display: inline;
  }
}



