.hsv {
  grid-area: 1 / 2;
  display: -ms-flexbox;
  display: flex;
  position: relative;
  -ms-touch-action: none;
      touch-action: none;
}

.canvas--picking {
  cursor: crosshair;
}

.canvas--picked {
  cursor: pointer;
}

.color--picked {
  position: absolute;
  right: 9vw;
  cursor: default;
  -webkit-animation: picked 0.1s ease-in;
          animation: picked 0.1s ease-in;
}

.range--value {
  -ms-flex-item-align: center;
      align-self: center;
  -webkit-transform: rotate(-90deg);
      -ms-transform: rotate(-90deg);
          transform: rotate(-90deg);
  color: var(--color-main);
  margin: 0 0.5rem;
}

.range--value__input {
  cursor: pointer;
  background: -webkit-gradient(linear, right top, left top, from(#fff), to(#000));
  background: -webkit-linear-gradient(right, #fff, #000);
  background: -o-linear-gradient(right, #fff, #000);
  background: linear-gradient(-90deg, #fff, #000);
}

@-webkit-keyframes picked {
  from {
    -webkit-transform: scale(2);
            transform: scale(2);
    opacity: 0;
  }
  to {
    -webkit-transform: scale(1);
            transform: scale(1);
    opacity: 1;
  }
}

@keyframes picked {
  from {
    -webkit-transform: scale(2);
            transform: scale(2);
    opacity: 0;
  }
  to {
    -webkit-transform: scale(1);
            transform: scale(1);
    opacity: 1;
  }
}



