.main {
  display: grid;
  -ms-flex-pack: center;
      justify-content: center;
  grid-template-columns: repeat(3, 1fr);
  grid-template-rows: repeat(4, auto);
  margin: 4vw;
}

@media (min-width: 1280px) {
  .main {
    margin: 4vw 8vw;
  }
}

@media (min-width: 1600px) {
  .main {
    margin: 4vw 12vw;
  }
}



