.color-input {
  grid-area: 2 / 2;
  -ms-flex-item-align: start;
      align-self: start;
  margin-top: 2rem;
  width: 50%;
  justify-self: left;
  color: var(--color-main);
}

.color-input__element {
  width: 100%;
}

@media (min-width: 600px) {
}

@media (min-width: 900px) {
  .color-input {
    width: 30%;
  }
}



